import { CHECKBOX_PRIMARY_STYLE_CHECKED, CHECK_BOX_STYLES } from "assets/base-theme";
import BaseCheckBox, { IBaseCheckBoxProps } from "components/base/BaseCheckBox";
import { IInputCommonStyleProps } from "components/interface/Input";
import React from "react";
import { INPUT_ERROR_TYPE } from "utils/Constant";
import { generateClasses } from "utils/Util";

interface ICheckBoxProps extends IBaseCheckBoxProps, IInputCommonStyleProps {}
const CheckBox: React.FC<ICheckBoxProps> = (props: ICheckBoxProps) => {
  const { variant, styleObj, ...BaseCheckBoxProps } = props;
  const getClassNames = () => {
    let styleObject = {
      ...CHECK_BOX_STYLES,
      overrideStyle: styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      isInvalid: !!props.validation,
      applyErrorStyle: props.validation && props.errorType == INPUT_ERROR_TYPE.BORDER,
      removeStyleProperty: [],
      removeClasses: [],
    };

    if (props.checked) {
      styleObject = { ...styleObject, primaryStyle: CHECKBOX_PRIMARY_STYLE_CHECKED };
      return generateClasses(styleObject, "PRIMARY");
    } else {
      return generateClasses(styleObject, variant);
    }
  };

  return (
    <>
      <BaseCheckBox className={getClassNames()} {...BaseCheckBoxProps} />
    </>
  );
};

// memoization

export default React.memo(CheckBox);
