import InputError from "components/InputError";
import Label from "components/Label/Label";
import { withAuth } from "components/hoc/withAuth";
import { IBaseInputCommonProps } from "components/interface/Input";
import { Checkbox, CheckboxProps } from "primereact/checkbox";
import React from "react";

export interface IBaseCheckBoxProps extends CheckboxProps, IBaseInputCommonProps {}

const BaseCheckBox: React.FC<IBaseCheckBoxProps> = (props: IBaseCheckBoxProps) => {
  const {
    hideLabel,
    label,
    labelClassNames,
    labelStyleObj,
    validation,
    hideErrorRow,
    errorClassNames,
    errorStyleObj,
    disabled,
    isDisabledByAcl,
    ...PrimeReactProps
  } = props;

  const { placeholder, className, value, style, name, onChange } = PrimeReactProps;
  return (
    <div className="group grid">
      {!hideLabel && <Label label={label} labelClassNames={labelClassNames} />}
      <Checkbox
        value={value}
        style={style}
        name={name}
        placeholder={placeholder}
        onChange={onChange}
        disabled={disabled || isDisabledByAcl}
        {...PrimeReactProps}
        className={`${className} ${
          isDisabledByAcl
            ? "!bg-secondary-disabledBGColor !border-primary-gray-300 placeholder-secondary-gray-500"
            : " "
        } `}
      />
      {!hideErrorRow && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
    </div>
  );
};

export default withAuth(BaseCheckBox);
